.app{
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    max-width: 415px;
    height: 897px;   
}


.form {
    width: 414px;
    height: 896px;

    /* background-image: url('/public/Fon.svg'); */


    /* background: linear-gradient(to right, rgba(3, 0, 0, 0.6), rgb(18 16 16 / 50%)), 
    url('/public/eldoc.jpg')
     5% 10% no-repeat ; */


     background: linear-gradient(to right, rgba(0, 0, 0, 0.6), rgb(18 16 16 / 50%)), 
    url('/public/ecm1000.jpg')
     50% 0 no-repeat ;


    background-size: cover;
    
    
}

.form_logo{
    display: flex;
    justify-content: right;
    margin: 26px 19px;
}
.form_logo img{
    width: 142px;
    height: 82px;
}

.authorization{
    width: 355px;
    height: 513px;
    margin: auto;
    margin: 90px 30px;
}


.form_kstu {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;

}

.form_kstu img{
    width: 180px;
    height: 180px;
}

.form_kstu p{
    font-size: 26px;
    font-weight: 700;
    color: #fff;
}

.form_int{
    display: flex;
    width: 312px;
    margin: auto;
    margin-bottom: 40px;
    height: 30px;
    position: relative;
    border-bottom: 2px solid #FFFFFF;
}

.icon {
    position: absolute;
    width: 23px;
    height: 22.5px;
}


.input{
    width: 100%;
    border: none;
    outline: none;
    background: none;
    margin-left: 40px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
}

.input::placeholder{
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
}



.form_button{
    display: flex;
    justify-content: center;
}

.form_btn {
    width: 319px;
    height: 45px;
    border-radius: 5px;
    border: none;
    font-weight: 700;
    font-size: 22px;
    color: #3F3F3F;
    margin-top: 5px;
    cursor: pointer;
}

